<template>
    <div class="scratchBox">
        <div ref="slot" class="slot" :style="{borderRadius: radius + 'px'}">
            <slot></slot>
        </div>
        <canvas v-if="width && height" class="canvas" ref="canvas" :width="width" :height="height"></canvas>
    </div>
</template>

<script setup lang="ts">
import { nextTick } from 'vue';
import { ref, defineProps, defineEmits, defineExpose, onMounted, onUnmounted } from 'vue';

const props = defineProps({
    scratchPercent: {
        type: Number,
        default: 80
    },
    imageUrl: {
        type: String
    },
    maskColor: {
        type: String,
        default: "#cccccc"
    },
    fillStyle: {
        type: String,
        default: "#000000"
    },
    font: {
        type: String,
        default: "20px Arial"
    },
    text: {
        type: String,
        default: "刮一刮"
    },
    radius: {
        type: Number,
        default: 0
    },
    scratchRadius: {
        type: Number,
        default: 10
    }
});

const emits = defineEmits([
    'scratchStart', 'scratchEnd', 'ScratchAll'
]);

const ctx = ref<CanvasRenderingContext2D | null | undefined>(null);
const slot = ref<HTMLInputElement | null>(null);
const canvas = ref<HTMLCanvasElement | null>(null);
const width = ref(0);
const height = ref(0);
const isScratch = ref(false);
const isScratchComplete = ref(false);

function init() {
    console.log('JN - init0');
    initCanvas();
    console.log('JN - init1');
    nextTick(() => {
        console.log('JN - init2');
        initDraw();
        console.log('JN - init3');
        bindEvents();
        console.log('JN - init4');
    });
}

function initCanvas() {
    width.value = slot.value?.offsetWidth || 0;
    height.value = slot.value?.offsetHeight || 0;
}

function initDraw() {
    ctx.value = canvas.value?.getContext("2d");
    if (ctx.value) {
        ctx.value.globalAlpha = 1;
        ctx.value.fillStyle = props.maskColor;
        if (props.radius) {
            const radius = props.radius;
            ctx.value.beginPath();
            //左上角圓角
            ctx.value.moveTo(radius, 0);
            ctx.value.arcTo(0, 0, 0, radius, radius);
            //左下角圓角
            ctx.value.lineTo(0, height.value - radius);
            ctx.value.arcTo(0, height.value, radius, height.value, radius);
            // 右下角圓角
            ctx.value.lineTo(width.value - radius, height.value);
            ctx.value.arcTo(
                width.value,
                height.value,
                width.value,
                height.value - radius,
                radius
            );
            //右上角圓角
            ctx.value.lineTo(width.value, radius);
            ctx.value.arcTo(width.value, 0, width.value - radius, 0, radius);

            ctx.value.closePath();
            ctx.value.clip();
        }
        ctx.value.fillRect(0, 0, width.value, height.value);

        if (!props.imageUrl) {
            ctx.value.fillStyle = props.fillStyle;
            ctx.value.font = props.font;

            let text = props.text;
            let textWidth = ctx.value.measureText(text).width;
            let x = width.value / 2 - textWidth / 2;
            let y = height.value / 2 + 6;

            ctx.value.fillText(text, x, y);
        } else {
            const img = new Image();
            img.src = props.imageUrl;

            img.onload = () => {
                ctx.value?.drawImage(img, 0, 0, width.value, height.value);
            }
        }
    }
}

function bindEvents() {
    if (!canvas.value) return;

    canvas.value.addEventListener("mousedown", (e) => {
        if (!isScratchComplete.value) {
            isScratch.value = true;
            drawArc(e);
        }
    });

    canvas.value,addEventListener("mousemove", (e) => {
        if (!isScratchComplete.value) {
            if (isScratch.value == true) {
                drawArc(e);
            }
        }
    });

    canvas.value,addEventListener("mouseup", (e) => {
        if (!isScratchComplete.value) {
            isScratch.value = false;
            calcArea();
        }
    });

    canvas.value.addEventListener("touchstart", (e) => {
        if (!isScratchComplete.value) {
            emits("scratchStart");
            isScratch.value = true;
            drawArc(e);
        }
    });

    canvas.value.addEventListener("touchmove", (e) => {
        if (!isScratchComplete.value) {
            if (isScratch.value == true) {
                drawArc(e);
            }
        }
    });

    canvas.value.addEventListener("touchend", (e) => {
        if (!isScratchComplete.value) {
            isScratch.value = false;
            emits("scratchEnd");
            calcArea();
        }
    });
}

function drawArc(e: MouseEvent | TouchEvent) {
    if (!canvas.value || !ctx.value) return;

    const canvasPos = canvas.value.getBoundingClientRect();
    const pageScrollTop = document.documentElement.scrollTop || document.body.scrollTop;
    const pageScrollLeft = document.documentElement.scrollLeft || document.body.scrollLeft;
    let mouseX = 0;
    let mouseY = 0;

    if (e instanceof MouseEvent) {
        mouseX = e.pageX - canvasPos.left - pageScrollLeft;
        mouseY = e.pageY - canvasPos.top - pageScrollTop;
    } else if(e instanceof TouchEvent) {
        mouseX = e.targetTouches[0].pageX - canvasPos.left - pageScrollLeft;
        mouseY = e.targetTouches[0].pageY - canvasPos.top - pageScrollTop;
    } else {
        return;
    }

    ctx.value.globalCompositeOperation = "destination-out";
    ctx.value.beginPath();
    ctx.value.fillStyle = "white";
    ctx.value.moveTo(mouseX, mouseY);
    ctx.value.arc(mouseX, mouseY, props.scratchRadius, 0, 3 * Math.PI);
    ctx.value.fill();
}

function reset() {
    console.log('JN - reset0');
    width.value = 0;
    console.log('JN - reset1');
    height.value = 0;
    console.log('JN - reset2');
    nextTick(() => {
        console.log('JN - reset3');
        init();
        console.log('JN - reset4');
        isScratchComplete.value = false;
    });
}

function calcArea() {
    if (!canvas.value || !ctx.value) return;

    let myImg = ctx.value.getImageData(0, 0, width.value, height.value);
    let num = 0;
    let max = myImg.data.length / 4;
    for (let i = 0; i < myImg.data.length; i+=4) {
        if (myImg.data[i+3] == 0) {
            num++;
        }
    }

    if (num >= max * (props.scratchPercent / 100)) {
        // canvas.value.remove();
        ctx.value.clearRect(0, 0, width.value, height.value);
        emits("ScratchAll");
        isScratchComplete.value = true;
    }
}

function onResize() {
    ctx.value = null;
    init();
}

onMounted(() => {
    init();
    window.addEventListener("resize", onResize);
});

onUnmounted(() => {
    window.removeEventListener("resize", onResize);
});

defineExpose({
    reset
});

</script>

<style scoped>

.scratchBox {
    position: relative;
    display: inline-block;
}

.slot {
    display: inline-block;
    overflow: hidden;
}

.canvas {
    position: absolute;
    top: 0;
    left: 0;
}

</style>